body {
  font-family: Arial, sans-serif;
  background-color: #ffffff;
}


.navbar {
  display: flex;
  justify-content: center;
  background-color: #333;
  height: 50px; /* Set a smaller height */
  position: fixed; /* Fix the position */
  top: 0; /* Position at the top of the viewport */
  left: 0; /* Align with the left edge */
  right: 0; /* Align with the right edge */
  z-index: 1000; /* Ensure it appears above other content */
}

.social-media {
  display: flex;
  flex-direction: column; /* Stack the links vertically */
}

.social-logo {
  width: 60px;   /* Adjust the size of the logos as needed */
  height: 60px;
  margin-bottom: 10px; /* Add some space between the logos */
}

.social-logo-linear {
  width: 60px;   /* Adjust the size of the logos as needed */
  height: 60px;
  margin-bottom: 10px; /* Add some space between the logos */
  margin-right: 10px;
  margin-left: 10px;
  transition: filter 0.3s ease-in-out; /* Smooth transition for the hover effect */
}
.subtitle {
  font-size: 18px;
}

.logo {
  font-size: 24px;
  font-weight: bold;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px; /* Space between menu items */
}

.nav-links li {
  display: inline-block;
}

.nav-links a {
  text-decoration: none;
  font-size: 16px; /* Font size */
  color: white; /* Default text color */
  background-color: transparent; /* Transparent background */
  padding: 10px 15px; /* Padding for button appearance */
  border-radius: 5px; /* Rounded corners */
  transition: color 0.3s; /* Smooth transition for color */
  border: 2px solid transparent; /* Optional: add a transparent border */
  font-weight: bold; /* Make text bold */
}

/* Hover effect */
.nav-links a:hover {
  color: red; /* Change text color to red on hover */
  border: 2px solid red; /* Optional: add a red border on hover */
}

.nav-links a.active {
  color: yellow; /* Change color to yellow (or any color you prefer) when active */
  border: 2px solid yellow; /* Change border color to match active state */
}

main {
  padding-top: 60px; /* Top padding */
  /*padding-right: 20px; /* Right padding */
  /*padding-bottom: 20px; /* Bottom padding */
  /*padding-left: 20px; /* Left padding */
  /*margin-left: 150px; /* Adjusted margin to accommodate 100px extension */
  /*margin-right: 150px; /* Adjusted margin to accommodate 100px extension */
  
  background-color: white;
  background-image: url('./images/background.jpg'); /* Replace with your image path */
  background-size: 100% 100vh; /* Set image to cover the viewport height */
  background-repeat: no-repeat; /* Ensure the image does not repeat */
  background-position: center top; /* Align the image at the top center */
  min-height: 100vh; /* Ensure the main content takes up the full viewport height */
}

.main-section {
  margin-left: 20vw;
  margin-right: 20vw;
}

section {
	padding-top: 60px; /* Top padding */
  margin-bottom: 40px;
}

footer {
    text-align: center;
    padding: 20px 0;
    background-color: #333;
    color: #fff;
}

footer a {
    color: #ffcc00;
    text-decoration: none;
}

footer a:hover {
    text-decoration: underline;
}

h1 {
  color: #333;
  font-size: 3em; /* Adjust this value to make the heading bigger */
}

h2 {
  color: #333;
  font-size: 3em;
  position: relative; /* Required for absolute positioning of the pseudo-element */
}

h2::after {
  content: ""; /* Required for the pseudo-element */
  display: block; /* Makes the pseudo-element a block element */
  width: 100%; /* Stretches the underline across the entire width */
  height: 2px; /* Thickness of the underline */
  background-color: #333; /* Color of the underline */
  position: absolute; /* Allows positioning relative to the h2 */
  bottom: 0; /* Positions the underline at the bottom of the h2 */
  left: 0; /* Aligns the underline to the left */
}

.image-gallery {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.image-item img {
  max-width: 100%;
  height: auto;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.profile-section {
  display: flex; /* Use flexbox for layout */
  align-items: center; /* Center items vertically */
  justify-content: center; /* Center items horizontally */
  margin-top: 60px; /* Increase top margin as needed */
  padding-bottom: 20vh; /* Base padding-bottom for smaller screens */
}

/* For medium-sized screens */
@media (min-height: 600px) {
  .profile-section {
    padding-bottom: 20vh; /* Increase padding-bottom for medium-sized screens */
  }
}

/* For medium-sized screens */
@media (min-height: 800px) {
  .profile-section {
    padding-bottom: 40vh; /* Increase padding-bottom for medium-sized screens */
  }
}

/* For larger screens */
@media (min-height: 1000px) {
  .profile-section {
    padding-bottom: 60vh; /* Further increase padding-bottom for larger screens */
  }
}

/* For very large screens */
@media (min-height: 1200px) {
  .profile-section {
    padding-bottom: 80vh; /* Max padding-bottom for very large screens */
  }
}

.profile-image {
	width: 150px; /* Set desired width for the profile image */
	height: auto; /* Maintain aspect ratio */
	margin-right: 20px; /* Space between image and text */
	border: 1px solid #ccc;
  border-radius: 4px;
}

.welcome-message {
  font-size: 10em; /* Increase font size for the welcome message */
  font-weight: bold; /* Make it bold */
  margin: 0; /* Remove default margin */
  color: #333; /* Change text color to white */
	text-align: center; /* Center the text */
}

.job-description {
  font-size: 1.2em; /* Set the font size for the job description */
  margin-top: 10px; /* Space between welcome message and description */
  color: #333;
  text-align: center; /* Center the text */
}

.scroll-link {
	text-decoration: none; /* Remove underline from link */
	color: inherit; /* Inherit color from parent (job-description) */
	font-weight: bold; /* Make it bold if desired */
}

.scroll-link:hover {
	color: red; /* Change color on hover if desired */
}
